import React from 'react';
import '../stylesheets/charts.css';
import Header from "../components/header";
import Footer from "../components/footer";
import {Link} from "react-router-dom";
import ChartListTemp from "../components/chartListTemp";
import PlayURL from "../components/getPlaylist";


class ChartsHour extends React.Component {
    render() {
        return (
            <div className="container">
                <Header/>
                <div className="fade-in-page">
                    <div className="news-header">
                        <h2>BILLBOARDOO CHART</h2>
                        <h1>DAILY 100</h1>
                    </div>
                    <hr className="chart-menu-line"/>
                    <div className="chart-menu">
                        <Link to="/charts" className="chart-menu-content"></Link>
                        <Link to="/charts/hourly" className="chart-menu-content"></Link>
                        <Link to="/charts/daily" className="chart-menu-content"></Link>
                        <Link to="/charts/weekly" className="chart-menu-content"></Link>
                        <Link to="/charts/monthly" className="chart-menu-content"></Link>
                    </div>
                    <hr className="chart-menu-line"/>
                    <div className="chart-info">
                        <Link to="/graphs/daily" className="chart-menu-content"> </Link>
                        {<PlayURL type="daily"/>}
                    </div>
                    <div className="chart-wrap">
                        <hr className="chart-separate-line"/>
                        <div className="chart-header">
                            <span id="header-pos"></span>
                            <span id="header-title"></span>
                            <span id="header-artist"></span>
                            <div className="header-right-align">
                                <span id="header-peak"></span>
                                <span id="header-date"></span>
                                <span id="header-views">{this.props.type === "total" ? "": ""}</span>
                            </div>
                        </div>
                        <hr className="chart-separate-line"/>
                        <ChartListTemp type="daily"/>
                    </div>
                </div>
                <Footer/>
            </div>
        );
    }
}

export default ChartsHour;
